import React, {useEffect, useState} from 'react';
import style from './home.module.css';
import {useAppDispatch, useAppSelector} from "../../hooks";
import {fetchGames} from "../../redux/slice/main/homeSlice";
import Slide from "./Slide";

const Slider = () => {
    const dispatch = useAppDispatch()
    const games = useAppSelector(state => state.home.slider)
    const [filter, setFilter] = useState('new')
    const [currentSlide, setCurrentSlide] = useState(0)

    useEffect(() => {
        dispatch(fetchGames('new'))
    }, [])

    const makeRequest = (filter: string) => {
        setFilter(filter)
        dispatch(fetchGames(filter))
    }

    const leftClick = () => {
        if (currentSlide < 2) {
            setCurrentSlide(currentSlide + 1);
        }
    }

    const rightClick = () => {
        if (currentSlide > -5) {
            setCurrentSlide(currentSlide - 1);
        }
    }

    return (
        <section className={style.slider}>
            <div className="container">
                <img className={style.slider__logo} src="./assets/sliderLogo.png" alt=""/>
                <div className={style.slider__filter}>
                    <ul className={style.slider__list}>
                        <li className={style.slider__item}>
                            <button
                                className={filter === 'new' ?
                                    style.slider__btn + ' ' + style.slider__btnActive :
                                    style.slider__btn
                                }
                                onClick={() => makeRequest('new')}>
                                
                            </button>
                        </li>
                        <li className={style.slider__item}>
                            <button
                                className={filter === 'sales' ?
                                    style.slider__btn + ' ' + style.slider__btnActive :
                                    style.slider__btn
                                }
                                onClick={() => makeRequest('sales')}>
                                
                            </button>
                        </li>
                        <li className={style.slider__item}>
                            <button
                                className={filter === 'popular' ?
                                    style.slider__btn + ' ' + style.slider__btnActive :
                                    style.slider__btn
                                }
                                onClick={() => makeRequest('popular')}>
                                
                            </button>
                        </li>
                        <li className={style.slider__item}>
                            <button
                                className={filter === 'release' ?
                                    style.slider__btn + ' ' + style.slider__btnActive :
                                    style.slider__btn
                                }
                                onClick={() => makeRequest('release')}>
                                
                            </button>
                        </li>
                        <li className={style.slider__item}>
                            <button
                                className={filter === 'pre-order' ?
                                    style.slider__btn + ' ' + style.slider__btnActive :
                                    style.slider__btn
                                }
                                onClick={() => makeRequest('pre-order')}>
                                
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
            <div className={style.slider__bottom}>
                <span onClick={leftClick} className={style.slider__bottomArrow + ' ' + style.slider__bottomArrowLeft}/>
                <span onClick={rightClick} className={style.slider__bottomArrow}/>
                <ul className={style.carousel} style={{transform: `translate(${348 * currentSlide}px)`}}>
                    {games.map(el => <Slide
                        key={el.id}
                        id={el.id}
                        slide={el.slide}
                        urlImg={el.urlImg}
                        setCurrentSlide={setCurrentSlide}
                        currentSlide={currentSlide}
                        title={el.title}
                        isAvailable={el.isAvailable}
                        platforms={el.platforms}
                        price={el.price}
                    />)}
                </ul>
            </div>
        </section>
    );
};


export default Slider;